:root {
  --primary-color: #0066ff;
  --secondary-color: #00d4ff;
  --accent-color: #ff6b35;
  --success-color: #00ff88;
  --warning-color: #ffaa00;
  --danger-color: #ff4757;
  --whatsapp-color: #25d366;
  --facebook-color: #1877f2;
  --instagram-color: #e4405f;
  --dark-bg: #0a0a0f;
  --dark-secondary: #1a1a2e;
  --dark-tertiary: #16213e;
  --text-primary: #ffffff;
  --text-secondary: #b0b3c7;
  --gradient-primary: linear-gradient(135deg, #0066ff, #00d4ff);
  --gradient-accent: linear-gradient(135deg, #ff6b35, #ff8e53);
  --gradient-success: linear-gradient(135deg, #00ff88, #00cc6a);
  --gradient-whatsapp: linear-gradient(135deg, #25d366, #128c7e);
  --gradient-facebook: linear-gradient(135deg, #1877f2, #42a5f5);
  --gradient-instagram: linear-gradient(135deg, #e4405f, #fd1d1d, #fcb045);
  --gradient-dark: linear-gradient(135deg, #0a0a0f, #1a1a2e);
}

.auth-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background: var(--dark-bg);
  position: relative;
}
.auth-page::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 20% 30%, rgba(0, 102, 255, 0.1) 0%, transparent 50%), radial-gradient(circle at 80% 70%, rgba(255, 107, 53, 0.1) 0%, transparent 50%);
  animation: authBackground 8s ease-in-out infinite alternate;
}

@keyframes authBackground {
  0% {
    opacity: 0.3;
    transform: scale(1);
  }
  100% {
    opacity: 0.6;
    transform: scale(1.1);
  }
}
.auth-container {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 25px;
  padding: 8rem;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 2;
  overflow: hidden;
}
.auth-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 30% 20%, rgba(0, 102, 255, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.auth-header {
  text-align: center;
  margin-bottom: 4rem;
  position: relative;
  z-index: 2;
}

.auth-icon {
  font-size: 4rem;
  margin-bottom: 2rem;
  animation: authPulse 2s ease-in-out infinite;
}

@keyframes authPulse {
  0%, 100% {
    transform: scale(1);
    opacity: 0.8;
  }
  50% {
    transform: scale(1.1);
    opacity: 1;
  }
}
.auth-title {
  font-size: 2rem;
  font-weight: 900;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.auth-subtitle {
  color: var(--text-secondary);
  font-size: 1rem;
  margin: 0;
}

.auth-form {
  position: relative;
  z-index: 2;
}
.auth-form .form-group {
  margin-bottom: 2rem;
}
.auth-form .form-label {
  display: block;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
  font-weight: 600;
  font-size: 0.9rem;
}
.auth-form .form-input {
  width: 100%;
  padding: 2rem;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
  font-size: 1rem;
  transition: all 0.3s ease;
  box-sizing: border-box;
  font-family: inherit;
}
.auth-form .form-input:focus {
  outline: none;
  border-color: var(--primary-color);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(0, 102, 255, 0.1);
}
.auth-form .form-input::placeholder {
  color: var(--text-secondary);
  opacity: 0.6;
}
.auth-form .auth-button {
  width: 100%;
  padding: 2rem;
  background: var(--gradient-primary);
  border: none;
  border-radius: 20px;
  color: white;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}
.auth-form .auth-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 102, 255, 0.4);
}
.auth-form .auth-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}
.auth-form .auth-button:hover::before {
  left: 100%;
}
.auth-form .auth-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}
.auth-form .auth-button:disabled:hover {
  transform: none;
  box-shadow: none;
}
.auth-form .error-message {
  background: rgba(255, 71, 87, 0.1);
  border: 1px solid rgba(255, 71, 87, 0.3);
  color: var(--danger-color);
  padding: 1rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  font-size: 0.9rem;
  display: none;
  animation: slideInDown 0.3s ease-out;
}
.auth-form .loading {
  display: none;
  text-align: center;
  color: var(--text-secondary);
  margin-top: 2rem;
  font-size: 0.9rem;
}
.auth-form .loading .loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(0, 102, 255, 0.3);
  border-top: 2px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: inline-block;
  margin-right: 0.5rem;
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.back-link {
  text-align: center;
  margin-top: 4rem;
  position: relative;
  z-index: 2;
}
.back-link a {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.3s ease;
}
.back-link a:hover {
  color: var(--primary-color);
}

@media (max-width: 767px) {
  .auth-container {
    padding: 4rem;
    margin: 1rem;
  }
  .auth-title {
    font-size: 1.8rem;
  }
  .auth-icon {
    font-size: 3rem;
  }
}